const ADDNEWTEXT = 'ADDNEWTEXT';
const UPDATEFORM = 'UPDATEFORM';
const PUT_DATA = 'PUT_DATA';

const initialState = {
    value: '',
    comments: [
        { id: 1, comment: 'Done' },
        { id: 2, comment: 'Ready' },
        { id: 3, comment: 'Gap' },
        { id: 4, comment: 'Saw' }
    ],
    pics: {}
};

const formPage = (state = initialState, action) => {
    switch (action.type) {
        case UPDATEFORM:
            console.log('ch');
            return {
                ...state.value = action.body,
                ...state
            }
        case ADDNEWTEXT: {
            let body = state.value;
            let newComment = {
                id: 5,
                comment: body
            };
            state.value = '';
            state.comments.push(newComment);
            console.log('sub');
            return {
                ...state
            }
        };
        case PUT_DATA: 
            return {
                ...state,
                pics: action.payload
            }
        
        default:
            console.log('def');
            return state;
    };
};

export const updateForm = (body) => ({ type: UPDATEFORM, body });
export const addNewText = () => ({ type: ADDNEWTEXT });
export const putData = (dataFromServer) => ({ type: PUT_DATA, payload: dataFromServer });

export default formPage;