import React from 'react';
import styles from './product.module.css';
import { connect } from 'react-redux';
import { pushToCart } from './../../redux/catalogReducer';
// import { useState } from 'react';

const Product = (props) => {

    const addToCart = () => {
        const dataItem = {id: props.id, pic: props.pic, title: props.title};
        props.pushToCart(dataItem);
    };

    return (
        <div className={styles.description}>
            <div className={styles.imageFrame}>
                <img src={props.pic} className={styles.image} alt='icon'/>
            </div>
            <div className={styles.text}>
                <p>{props.title}({props.idc})</p>
            </div>
            <div className={styles.btnInfo}>
                <button>Більше Інфо</button>
                <button onClick={addToCart}>Додати в корзину</button>
            </div>
        </div>
    )
};

let mapStateToProps = (state) => {
    return {

    }
};
let mapDispatchToProps = (dispatch) => {
    return {
        pushToCart: (dataItem) => {
            dispatch(pushToCart(dataItem));
        }
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(Product);