import React from 'react';
import { Formik, Form, Field, ErrorMessage } from 'formik';

const CommentForm = (props) => {

    const comments = props.formPage.comments.map(c => {
        return <li key={c.id}>{c.comment}</li>
    });

    let newComment = (e) => {
        let body = e.target.value;
        props.newComment(body);
    };
    
  let addComment = (e) => {
      e.preventDefault();  
      props.addNewText();
    };


  return (
    <div>
      <h1>Form of Formik</h1>
      <div>{comments}</div>
      <Formik
        initialValues={{ text: '' }}
      >
        {({isSubmitting}) => (
          <Form onSubmit={addComment}>
            <Field type="text" name="text" value={props.formPage.value} onChange={newComment} />
            <ErrorMessage name="text" component="div" />            
            <button type="submit" disabled={isSubmitting}>
                Submit
            </button>
          </Form>
        )}
      </Formik>
    </div>)

};



export default CommentForm;