import React from 'react';
import Product from '../Product/Product';
import styles from './products.module.css';
import { connect } from 'react-redux';
import Cart from '../Cart/Cart';

const Products = (props) => {

    const alize = props.alize;
    const yarnArt = props.yarnArt;
    
    const renderProducts = (props) => {
        return props.map(item => {
            return <Product id={item.id} pic={item.pic} title={item.title} idc={item.idc} />
        });
    };

    // des={item.description}

    return (
        <div className={styles.productsContainer}>
            <div className={styles.listBox}>
                <h1>TITLE PRODUTS</h1>
                <h2>Alize</h2>
                <div className={styles.productsList}>
                    {renderProducts(alize)}
                </div>
                <h2>YarnArt</h2>
                <div className={styles.productsList}>
                    {renderProducts(yarnArt)}
                </div>
            </div>
            {props.cart.length === 0 ? null : <div className={styles.cart}>
                <h1>Alize</h1>
                <p>Hello yarn</p>
                <Cart />
            </div>}
        </div>
    )
};

let mapStateToProps = (state) => {

    return {
        alize: state.catalogPage.alize,
        yarnArt: state.catalogPage.yarnArt,
        cart: state.catalogPage.cart
    }
};

export default connect(mapStateToProps)(Products);
