import puffy3 from './../components/Image/PUFFY FINE_3_Khaki.jpg';
import puffy39 from './../components/Image/PUFFY FINE_39_Pink.jpg';
import puffy55 from './../components/Image/PUFFY FINE_55_White.jpg';
import puffy56 from './../components/Image/PUFFY FINE_56_Red.jpg';

const PUSH_TO_CART = 'PUSH_TO_CART';

const initialState = {
    alize: [
        { id: 1, pic: puffy3, title: 'Puffy Fine3', idc: 3, description: '3 мятный Рекомендуемый номер спиц: 4 ― 5 Рекомендуемый номер крючка: 2 ― 4' },
        { id: 2, pic: puffy39, title: 'Puffy Fine39', idc: 39, description: '39 светлый беж Рекомендуемый номер спиц: 4 ― 5 Рекомендуемый номер крючка: 2 ― 4' },
        { id: 3, pic: puffy55, title: 'Puffy Fine55', idc: 55, description: '55 карамель Рекомендуемый номер спиц: 4 ― 5 Рекомендуемый номер крючка: 2 ― 4' },
        { id: 4, pic: puffy56, title: 'Puffy Fine56', idc: 56, description: '55 карамель Рекомендуемый номер спиц: 4 ― 5 Рекомендуемый номер крючка: 2 ― 4' },
        { id: 5, pic: '', title: '', description: '' },
        { id: 6, pic: '', title: '', description: '' },
        { id: 7, pic: '', title: '', description: '' },
        { id: 8, pic: '', title: '', description: '' },
        { id: 9, pic: '', title: '', description: '' },
        { id: 10, pic: '', title: '', description: '' }
    ],
    yarnArt: [
        { id: 1, pic: '', title: '', description: '' },
        { id: 2, pic: '', title: '', description: '' },
        { id: 3, pic: '', title: '', description: '' },
        { id: 4, pic: '', title: '', description: '' },
        { id: 5, pic: '', title: '', description: '' },
        { id: 6, pic: '', title: '', description: '' },
        { id: 7, pic: '', title: '', description: '' },
        { id: 8, pic: '', title: '', description: '' },
        { id: 9, pic: '', title: '', description: '' },
        { id: 10, pic: '', title: '', description: '' }
    ],
    cart: []
};

const catalogPage = (state = initialState, action) => {
    switch (action.type) {
        case PUSH_TO_CART: 
            return {
                ...state,
                cart: [...state.cart, action.payload]
            }
        default:
            return state;
    };
};

export const pushToCart = (dataItem) => ({ type: PUSH_TO_CART, payload: dataItem});

export default catalogPage;