import React from 'react';
import { connect } from 'react-redux';
import ItemInCart from './../Common/ItemInCart/ItemInCart';
import styles from './cart.module.css';

const Cart = (props) => {
    debugger;
    const cartItems = props.cart;
    const renderCart = cartItems.map(i => {
        return <ItemInCart pic={i.pic} title={i.title} />
    });

    return (
        <div>
            cart1
            <div className={styles.box}>
                {renderCart}
            </div>
        </div>
    )
};

let mapStateToProps = (state) => {
    return {
        cart: state.catalogPage.cart
    }
}

export default connect(mapStateToProps)(Cart);